import Vue from 'vue'
import Vuex from 'vuex'

import { marketService } from '@/services/marketers.service';
import { storageService } from '@/services/storage.service';

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    marketers: [],
    didApply: false
  },
  getters: {
    marketersCount(state) {
      return state.marketers.length;
    },
    didApply(state) {
      return state.didApply;
    }
  },
  mutations: {
    setMarketers(state, { marketers }) {
      state.marketers = marketers;
    },
    checkApplicant(state) {
      state.didApply = !!storageService.load('applied');
    },
    markApplication(state) {
      state.didApply = true;
    }
  },
  actions: {
    async loadMarketers(context) {
      const marketers = await marketService.query();
      context.commit({type: 'setMarketers', marketers});
    }
  }
})
