import { httpService } from './http.service';

export const marketService = {
   query,
   save
}

async function query(criteria = {}) {
    const params = new URLSearchParams(criteria); 
    return httpService.get(`application?${params}`);
}

async function save(marketer) {                        
    let prm;
    if (marketer._id) prm = httpService.put(`application/${marketer._id}`, marketer);
    else {
        prm = httpService.post('application', marketer);
    }
    return await prm;
}